:root {
  box-sizing: border-box;
  font-size: calc(0.5em + 1vw);
  line-height: calc(1em + 1vw);
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: inherit;
}

html,
body {
  height: 100%;
  font-weight: 400;
  font-family: Tahoma, Verdana, Arial, Sans-Serif;
  font-size: inherit;
  line-height: inherit;
  scroll-behavior: smooth;
  color: black;
  background: #fdfbfb;
  background: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
}

a {
  text-decoration: none;
}

ul,
ol,
li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
b {
  font-weight: inherit;
  font-size: inherit;
}

.container {
  margin: 0 auto;
  max-width: 95vw;
  min-height: 5rem;
}
@media (min-width: 270px) {
  .container {
    max-width: 95%;
  }
}
@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}
@media (min-width: 1400px) {
  .container {
    max-width: 1320px;
  }
}

.wrapper {
  max-width: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-around;
  padding: 2rem;
}

[class*=col-] {
  display: block;
}

.col-1 {
  width: calc(100% / 1);
}

.col-2 {
  width: calc(100% / 2);
}

.col-3 {
  width: calc(100% / 3);
}

.col-4 {
  width: calc(100% / 4);
}

.col-5 {
  width: calc(100% / 5);
}

.col-6 {
  width: calc(100% / 6);
}

.col-7 {
  width: calc(100% / 7);
}

.col-8 {
  width: calc(100% / 8);
}

.col-9 {
  width: calc(100% / 9);
}

.col-10 {
  width: calc(100% / 10);
}

.col-11 {
  width: calc(100% / 11);
}

.col-12 {
  width: calc(100% / 12);
}

.body,
.js-focus-visible {
  background: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
}

/* --- header & menu styles --- */
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  background: #fdfbfb;
  background: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
  border-bottom: 0.2rem solid #dbd7d7;
}
.header__wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  max-width: 95vw;
  min-height: 5rem;
  padding: 0 1.5rem;
}
.header__logo {
  display: flex;
  flex: 1 1 auto;
  color: black;
  text-transform: uppercase;
  font-weight: 700;
  padding: 0.5rem;
  position: relative;
  z-index: 5;
  border-bottom: 0.01rem solid transparent;
}
.header__logo:hover {
  border-bottom: 0.1rem solid transparent;
}
.header__lang {
  z-index: 5;
  width: 5rem;
  height: 5rem;
  display: block;
  color: #000;
  padding-top: 1.7rem;
  padding-left: 3.7rem;
  background-size: 2rem;
  background-repeat: no-repeat;
  background-position: center;
  font-weight: 600;
}
.header__lang--toRu {
  background-image: url("/img/lang/en_icon.png");
}
.header__lang--toEn {
  background-image: url("/img/lang/ru_icon.png");
}

.menu__icon {
  display: none;
}
.menu__link {
  color: black;
  text-transform: uppercase;
  font-weight: 700;
  padding: 0.5rem;
  border-bottom: 0.01rem solid transparent;
}
.menu__link:hover {
  border-bottom: 0.2rem solid #0000a1;
}
.menu__list > li {
  position: relative;
  margin-left: 2rem;
}

/* --- pc / touch begin --- */
body._touch .menu__link {
  flex: 1 1 auto;
}
body._touch .menu__list > li {
  display: flex;
  align-items: center;
}

/* --- pc / touch end --- */
/* --- media begin --- */
@media (min-width: 768px) {
  .menu__list {
    display: flex;
    align-items: center;
    flex-direction: row;
  }
}
@media (max-width: 768px) {
  .menu__icon {
    z-index: 5;
    display: block;
    position: relative;
    cursor: pointer;
  }

  .menu__icon span,
.menu__icon::before,
.menu__icon::after {
    display: block;
    content: "";
    width: 2.5rem;
    height: 0.35rem;
    position: absolute;
    right: 0;
    transition: all 0.3s ease 0s;
    background-color: black;
  }

  .menu__icon::before,
.menu__icon::after {
    content: "";
  }

  .menu__icon::before {
    top: -1rem;
  }

  .menu__icon::after {
    bottom: -1rem;
  }

  .menu__icon span {
    top: 50%;
    transform: scale(1) translate(0, -50%);
  }

  .menu__icon._active span {
    transform: scale(0) translate(0, -50%);
  }

  .menu__icon._active::before {
    top: 0;
    transform: rotate(-45deg) translate(0, 0);
  }

  .menu__icon._active::after {
    top: 0;
    transform: rotate(45deg) translate(0, 0);
  }

  .menu__body {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: #fdfbfb;
    background-color: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
    padding: 1rem;
    padding-top: 6rem;
    transition: left 0.3s ease 0s;
    overflow: auto;
  }

  .menu__body._active {
    left: 0;
    text-align: center;
  }

  .menu__body._active > li {
    padding: 2rem;
    box-shadow: 0 0 0.5rem 0.1rem #0000a1;
  }

  .menu__body::before {
    content: "";
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    height: 4rem;
    background-color: #fdfbfb;
    background-color: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
    z-index: 2;
  }

  .menu__list {
    flex-direction: column;
  }

  .menu__list > li {
    flex-wrap: wrap;
    margin: 0;
    margin-bottom: 3rem;
  }

  .menu__list > li:last-child {
    margin-bottom: 0;
  }
}
/* --- media end --- */
/* --- header & menu styles end --- */
/* --- main section styles begim --- */
.section {
  max-width: 95vw;
}

.title {
  color: black;
  text-align: center;
}

.subtitle {
  text-align: center;
}

.about {
  margin: 1.5rem auto;
}
.about__title {
  margin-top: 10rem;
  flex-grow: 1;
}
.about__text {
  padding-top: 1rem;
  padding-left: 3rem;
  background-image: url("/img/developer_icon.png");
  background-size: 2rem;
  background-repeat: no-repeat;
  background-position: left center;
}

.technologies__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 0.5rem;
}
.technologies__item {
  margin: 0.5rem;
}

.technology {
  display: block;
  min-width: 10rem;
  max-width: 15rem;
  min-height: 5rem;
  padding: 0.5rem;
  border-radius: 1rem;
  background: #fdfbfb;
  background: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
  border: 0.1rem solid #dbd7d7;
  text-align: center;
}
.technology__img {
  width: 4rem;
  height: 4rem;
}
.technology__text {
  color: inherit;
  font-size: inherit;
}

.certificats__wrapper {
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.certificat {
  display: block;
}
@media (min-width: 270px) {
  .certificat {
    max-width: 95%;
  }
}
@media (min-width: 768px) {
  .certificat {
    max-width: 10rem;
  }
}
.certificat__img {
  margin: 1.1rem auto;
  display: block;
  width: 100%;
  border: 0.1rem solid black;
  border-radius: 1rem;
}
.certificat__img:hover, .certificat__img:focus {
  transform: scale(1.1);
}
.works__item {
  margin: 1rem;
  border: 0.05rem solid transparent;
  box-shadow: 0 0 0.2rem 0.1rem #fdfbfb;
  background: #dbd7d7;
}
.works__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0;
}

.work {
  background: #fdfbfb;
  background: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
  display: flex;
  flex-direction: column;
  border: 0.06rem solid transparent;
  box-shadow: 0 0 0.5rem 0.1rem #dbd7d7;
}
@media (min-width: 270px) {
  .work {
    max-width: 95%;
  }
}
@media (min-width: 768px) {
  .work {
    max-width: 46%;
  }
}
.work__img {
  display: block;
  width: 90%;
  margin: 0 auto;
  border: 0.06rem solid transparent;
  box-shadow: 0 0 0.5rem 0.1rem #dbd7d7;
}
.work__links {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 0 auto;
}
.work__link {
  color: black;
  margin: 1rem auto;
  text-align: center;
  width: 15rem;
  padding: 0.5rem;
  border: 0.1rem solid transparent;
  box-shadow: 0 0 0.1rem 0.1rem black;
  text-transform: uppercase;
}
@media (min-width: 1200px) {
  .work__link {
    margin: 1rem;
  }
}
.work__link:hover, .work__link:focus {
  border: 0.1rem solid transparent;
  background-color: #0000a1;
  color: white;
  box-shadow: 0 0 0.1rem 0.01rem #dbd7d7;
  opacity: 0.8;
}
.work__text {
  padding: 1rem;
  font-weight: 500;
  flex: 1 1 auto;
}
.work__title {
  color: black;
  padding: 0.5rem;
  box-shadow: 0 0 0.5rem 0.1rem #dbd7d7;
}

/* --- main section styles end --- */
/* --- footer styles begin --- */
.footer {
  border-top: 0.2rem solid #dbd7d7;
}
.footer__subtitle {
  padding: 1rem;
  text-align: center;
}
.contacts {
  padding: 1rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
}
.contacts__link {
  display: block;
  color: black;
  padding-left: 4rem;
  padding-bottom: 1rem;
  border-bottom: 0.2rem solid transparent;
  background-size: 2rem;
  background-repeat: no-repeat;
}
.contacts__link:hover, .contacts__link:focus {
  border-bottom: 0.2rem solid #0000a1;
  transform: scale(1.1);
}
.contacts__link--email {
  background-image: url("/images/contacts/email_icon.png");
}
.contacts__link--github {
  background-image: url("/images/contacts/github_logo.png");
}
.contacts__link--codepen {
  background-image: url("/images/contacts/codepen_logo.png");
}

/* --- footer styles end --- */
.animation--fadeInUp {
  -webkit-animation-name: animation--fadeInUp;
  animation-name: animation--fadeInUp;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

@-webkit-keyframes animation--fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
  100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}
@keyframes animation--fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
  100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}